body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: white;
}
.App-header{
  padding: 20px;
  margin-bottom: 0%;
  /* border: 1px solid #ccc; */
}
.App-body{
  margin: 0%;
  background-color: #efeded;
  height: 100vh;
  /* border: #000 1px solid; */
}
.popup-container{
  padding: 10px;
}
.popup {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}
.grouped-row {
  display: flex;
  justify-content: space-between;
  /* border: #000 1px solid; */
  /* margin: 20px 0; */

  background-color: #efeded;
}
.grouped-column {
  flex: 1;
  background-color: #fff;
  /* border: #000 1px solid; */
  padding: 20px;
  margin-right: 20px;
  background-color: #efeded;
}
.id {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #777;
  font-size: 12px;
}
.user-dp{
  padding: 5px;
  border-radius: 50%;
}
.head{
  display: flex;
  justify-content: space-between;
  /* border: #000 1px solid; */
  padding: 1px;
  color: #000;
  font-weight: bold;
  font-size: 18px;
}
.tag{
  display: flex;
  align-items: center;
}
.comp{
  display: flex;
  justify-content: space-between;
  /* border: #000 1px solid; */
  align-items: center;
  font-size: 15px;
}
.group-count{
  color: #777;
}
.title {
  color: #000;
  font-weight: bold;
  font-size: 12px;
}
.title2{
  display: flex;
  color: #000;
  font-weight: bold;
  font-size: 12px;
}
.card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
}

.card p {
  margin: 0;
}

.card h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px;
}
